.main{
  padding: 12.5em 0;
  text-align: center;
  overflow: hidden;
  &__inner{
    max-width: 100%;
    width: 70em;
    margin: 0 auto;
    text-align: center;
    position: relative;
  }
  &__bg{
    @include centerAbsolute;
    pointer-events: none;
    width: 103.75em;
  }
  &__title{
    font-size: 6.25em;
    line-height: 105%;
    font-weight: 700;
    &-bg{
      display: inline-block;
      position: relative;
      &:before,
      &:after{
        content: '';
        position: absolute;
        z-index: -1;
        width: calc(100% + .24em);
        height: .96em;
        left: -.12em;
        top: .12em;
        border-radius: 1.875em;
      }
      &:before{
        background: #FFF;
        transform: rotate(2deg);
      }
      &:after{
        background: #FCC6FC;
        transform: rotate(-2deg);
      }
    }
    img{
      width: 2.16em;
      display: inline-block;
      transform: translateY(15%);
    }
  }
  &__desc{
    margin: 1.75em auto 0;
    width: 62.25em;
    max-width: 100%;
  }
  &__nav{
    margin-top: 3em;
    display: flex;
    justify-content: center;
  }
  &__btn{
    img{
      margin-left: 1em;
    }
  }
}

.intro{
  padding: 8.75em 0;
  background: #B5CC9E;
  max-width: calc(100% - 4em);
  width: 100%;
  margin: 0 auto;
  border-radius: 1.5em;
}

.profitable{
  padding: 1.5em;
  background-color: $black;
  border-radius: 1.5em;
  &__top{
    text-align: center;
    padding: 1.5em;
    border-radius: 1.5em;
    border: 1px solid rgba(255, 255, 255, 0.15);
    box-shadow: $shadow;
    color: #fff;
    @include center;
    .text32{
      margin: 0 .5em;
    }
    img{
      width: 2em;
      flex-shrink: 0;
    }
  }
  &__grid{
    margin-top: 1.5em;
  }
  .profitable-slider{
    @media screen and (min-width: 993px){
      grid-column: 2/3;
      grid-row: 1/3;
    }
  }
}

.cashback-card,
.cashback-card2{
  &:hover{
    color: $black;
    .cashback-size{
      background-size: 1.5em auto;
      &__inner{
        opacity: 0;
      }
    }
  }
}

.cashback-card{
  border-radius: 2em;
  background-color: #fff;
  box-shadow: $shadow;
  padding: 1.5em;
  display: grid;
  grid-template-columns: 14.625em calc(100% - 14.625em - 1.5em);
  gap: 1.5em;
  align-items: flex-start;
  &__img{
    flex-shrink: 0;
    border-radius: 1.5em;
    border: $border;
    padding: .75em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__bottom{
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 3.875em;
    padding-left: 1em;
    margin-top: 1.5em;
    border-radius: 1.5em;
    background-color: $bg;
  }
  .cashback-size{
    background-color: $primary;
  }
}

.cashback-size{
  display: flex;
  align-items: center;
  padding: 0 1.19em;
  min-width: 4.9375em;
  height: 3.875em;
  box-shadow: $shadow;
  background-color: $bg;
  background-image: url(../img/arrow-right-white.svg);
  @include bgImgSize(0);
  justify-content: center;
  border-radius: 1.5em;
  transition: all .3s;
  &__inner{
    display: flex;
    align-items: flex-start;
    .text18{
      margin-left: .2em;
    }
  }
}

.profitable-slider{
  box-shadow: $shadow;
  border-radius: 2em;
  overflow: hidden;
  position: relative;
  .swiper{
    &-container{
      height: 100%;
    }
    &-nav{
      position: absolute;
      right: 1.5em;
      bottom: 1.5em;
      display: flex;
      z-index: 3;
    }
    &-button{
      position: static;
      margin: 0 0 0 .5em;
    }
  }
}

.banner{
  height: 100%;
  min-height: 21em;
  background-image: url(../img/banner-bg.svg);
  background-size: cover;
  background-position: center;
  border-radius: 2em;
  padding: 1.5em;
  display: flex;
  flex-direction: column;
  position: relative;
  overflow: hidden;
  .text40{
    max-width: 6.5em;
  }
  &__nav{
    margin-top: auto;
    padding-top: 2em;
    display: flex;
  }
  &__img{
    position: absolute;
    bottom: 0;
    right: 0;
    width: 19.6em;
  }
}

.month-cashback{
  margin-top: 8.75em;
  &:first-child{
    margin-top: 0;
  }
  .text60{
    text-align: center;
  }
  &__grid{
    margin-top: 3em;
  }
}

.cashback-card2{
  padding: 1.5em;
  border-radius: 2em;
  background-color: #fff;
  box-shadow: $shadow;
  display: flex;
  flex-direction: column;
  &:hover{
    .cashback-size{
      background-color: $primary;
    }
  }
  &__top{
    padding: .75em;
    border-radius: 1.5em;
    border: $border;
    min-height: 9.375em;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
  }
  &__img{
    width: 100%;
    flex-shrink: 0;
    margin-bottom: 1em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__bottom{
    padding-top: 2.25em;
    margin-top: auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .cashback-size{
    margin-left: .75em;
    flex-shrink: 0;
  }
}

.month-all{
  background-color: $black;
  border-radius: 2em;
  padding: 1.5em;
  color: #fff;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  &__top{
    padding: .75em;
    min-height: 9.375em;
    border-radius: 1.5em;
    border: 1px solid rgba(255, 255, 255, 0.15);
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    img{
      width: 2em;
      display: block;
    }
  }
  &__text{
    margin-top: auto;
    padding-top: 1em;
  }
  &__btn{
    margin-top: 1em;
    white-space: nowrap;
    img{
      margin-left: 1em;
    }
  }
}

.categories{
  padding: 8.75em 0 6.25em;
  .text60{
    text-align: center;
  }
  &__grid{
    margin-top: 3em;
  }
}

.category-card{
  display: grid;
  grid-template-columns: 11.25em calc(100% - 12.75em);
  gap: 1.5em;
  border-radius: 2em;
  background: #FFF;
  padding: 1.5em;
  box-shadow: $shadow;
  &:hover{
    color: $black;
    .category-card__arrow{
      background-color: $primary;
    }
  }
  &__img{
    display: block;
    border-radius: 1.5em;
    @include imgCover(72.2%);
  }
  &__content{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
  .text24{
    line-height: 110%;
  }
  &__bottom{
    padding-top: 1em;
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
  }
  &__count{
    box-shadow: $shadow;
    padding: 0 .75em;
    height: 2em;
    display: flex;
    align-items: center;
    background-color: $primary;
    border-radius: 1.5em;
  }
  &__arrow{
    width: 3.75em;
    height: 3.75em;
    border-radius: 1.5em;
    @include center;
    background-color: $bg;
    box-shadow: $shadow;
    transition: all .3s;
    img{
      width: 1.5em;
    }
  }
}

.all-categories{
  background-color: $black;
  padding: 1.5em;
  border-radius: 2em;
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  color: #fff;
  &__content{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;
  }
  &__num{
    font-size: 5em;
    line-height: 110%;
    font-weight: 700;
  }
}


@media screen and (max-width: 992px){
  .main{
    padding: 8.75em 0;
    &__title{
      font-size: 3.75em;
      img{
        width: 2.73em;
        transform: translateY(25%);
      }
    }
    &__bg{
      width: 66em;
    }
  }

  .intro{
    max-width: 100%;
  }

  .month-cashback{
    @media screen and (min-width: 577px){
      .text60 br,
      ._hide-tablet{
        display: none;
      }
    }
  }

  .month-all{
    &__btn{
      img{
        display: none;
      }
    }
  }

  .categories{
    padding-bottom: 2.5em;
  }

  .category-card{
    display: flex;
    flex-direction: column;
    gap: unset;
    &__img{
      flex-shrink: 0;
      padding-bottom: 43.3%;
      margin-bottom: 1.5em;
    }
    &__content{
      flex-grow: 1;
    }
  }

  .all-categories{
    flex-direction: column;
    align-items: unset;
    &__content{
      height: auto;
      margin-bottom: 1em;
      .text16{
        margin-top: .75em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .main{
    padding: 5em 0;
    &__title{
      font-size: 2.5em;
      line-height: 110%;
      img{
        width: 3.4em;
        transform: translateY(25%);
      }
      &-bottom{
        display: inline-block;
        transform: translateY(-.375em);
      }
      &-bg{
        &:before,
        &:after{
          height: 1.2em;
          width: calc(100% + .6em);
          top: -.05em;
        }
      }
    }
    &__desc{
      margin-top: 0;
    }
    &__nav{
      margin-top: 2em;
    }
    &__bg{
      width: 44em;
    }
  }

  .intro{
    padding: 5em 0;
  }

  .profitable{
    padding: 1em;
    &__top{
      padding: 1em 1.5em;
      .text32{
        margin: 0 .33em;
      }
    }
  }

  .cashback-card{
    padding: 1em;
    grid-template-columns: 100%;
    gap: 1em;
    &__bottom{
      margin-top: 1em;
    }
  }

  .profitable-slider{
    .swiper{
      &-nav{
        right: 1em;
        bottom: 1em;
      }
    }
  }

  .banner{
    padding: 1em;
    &__img{
      width: 17.5em;
    }
  }

  .month-cashback,
  .categories{
    &__grid{
      margin-top: 2em;
      display: flex;
      gap: unset;
      @include fullScroll;
    }
    .all-categories,
    .category-card,
    .month-all,
    .cashback-card2{
      width: 16.5em;
      flex-shrink: 0;
    }
  }

  .month-cashback{
    margin-top: 5em;
    .cashback-card2{
      margin-right: 1em;
    }
  }

  .cashback-card2{
    padding: 1em;
    &__top{
      margin-bottom: 1em;
    }
    &__bottom{
      padding-top: unset;
      border-radius: 1.5em;
      background-color: $bg;
      padding-left: 1em;
    }
    .cashback-size{
      background-color: $primary;
    }
  }

  .categories{
    padding: 5em 0 3.75em;
    .category-card{
      margin-right: 1em;
    }
  }

  .category-card{
    padding: 1em;
    &__img{
      height: 8.125em;
      padding: 0;
    }
  }

  .all-categories{
    padding: 1em;
  }
}
