.lk{
  padding: 7.5em 0 0;
  &__content{
    margin-top: 1.5em;
  }
}

.lk-card,
.lk-greet{
  padding: 1.5em;
  border-radius: 2em;
  display: flex;
  flex-direction: column;
  .advantage-card__icon{
    width: 3.25em;
    height: 3.25em;
  }
}

.lk-greet{
  grid-column: 1/3;
  background-color: $black;
  background-image: url(../img/greet-bg.svg);
  background-size: cover;
  background-position: center;
  .text24{
    color: #fff;
  }
  &__bottom{
    padding-top: 4.125em;
    margin-top: auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}

.account-type{
  display: flex;
  align-items: center;
  padding: .625em 1.25em;
  border-radius: 1.5em;
  background-color: $bg;
  box-shadow: $shadow;
  img{
    width: 2em;
    flex-shrink: 0;
    margin-right: .5em;
  }
  .text12{
    color: #828B9A;
  }
}

.lk-card{
  background: #B5CC9E;
  &__nums{
    display: flex;
    align-items: center;
  }
  &__num{
    &:not(:last-child){
      display: flex;
      align-items: center;
      &:after{
        content: '';
        width: 1em;
        height: 1em;
        flex-shrink: 0;
        background-image: url(../img/point.svg);
        @include bgImgSize(100%);
        margin: 0 .25em;
      }
    }
  }
  &__desc{
    margin-top: .25em;
  }
  &__bottom{
    margin-top: auto;
    display: flex;
    padding-top: 2.5em;
    .m-btn{
      height: 3.25em;
    }
  }
}

.lk-nav{
  margin-top: 5em;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: .75em;
  border-radius: 2em;
  border: 1px solid #D5D4CE;
  &__tabs{
    display: flex;
  }
}

.tab-content{
  display: none;
  &._active{
    display: block;
  }
}

.wallets,
.withdraw,
.stat,
.awards{
  padding: 1.5em;
  background-color: #fff;
  border-radius: 2em;
  box-shadow: $shadow;
}

.stat{
  &__items{
    margin-top: .75em;
    @include grid100gap(.75em);
  }
  &__item{
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    padding: .94em 1.5em;
    border-radius: 1.5em;
    background: #F3F1E9;
    box-shadow: $shadow;
    .lk-card__nums{
      flex-shrink: 0;
      margin-left: 1em;
    }
  }
}

.buy-stat{
  display: flex;
  justify-content: space-between;
  padding: 1.5em;
  margin-top: 1.5em;
  border-radius: 1.5em;
  border: $border;
  .text16{
    margin-top: .75em;
  }
  &__icon{
    margin-left: .75em;
    flex-shrink: 0;
  }
}

.awards{
  display: flex;
  flex-direction: column;
  &__steps{
    margin-top: 1.5em;
    position: relative;
  }
  &__progress{
    position: absolute;
    width: 100%;
    height: .125em;
    left: 0;
    bottom: 2.94em;
    background-color: $sup;
    span{
      position: absolute;
      top: 0;
      left: 0;
      height: 100%;
      background-color: $primary;
    }
  }
  &__bottom{
    margin-top: auto;
    padding-top: 1em;
  }
  &__hint{
    display: flex;
    align-items: flex-start;
    img{
      width: 1.5em;
      flex-shrink: 0;
      margin-right: .5em;
    }
  }
}

.badge{
  padding: 0 .75em;
  background-color: $primary;
  height: 2em;
  border-radius: 1.5em;
  box-shadow: $shadow;
  white-space: nowrap;
  display: flex;
  align-items: center;
  span{
    font-size: .875em;
    line-height: 140%;
  }
}

.award-step{
  text-align: center;
  padding-top: 3.5em;
  display: flex;
  flex-direction: column;
  position: relative;
  &:after{
    content: '';
    position: absolute;
    bottom: 2.63em;
    left: 50%;
    transform: translate(-50%,0);
    width: 0.75em;
    height: 0.75em;
    border-radius: 50%;
    border: .125em solid $sup;
    background-color: #fff;
    z-index: 2;
  }
  &__badge{
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    opacity: 0;
  }
  &._done{
    &:after{
      border-color: $primary;
    }
  }
  &._current{
    &:after{
      border-color: $primary;
      background-color: $primary;
    }
    .award-step__badge{
      opacity: 1;
    }
  }
  &__img{
    margin-bottom: .75em;
    height: 3em;
    display: flex;
    justify-content: center;
    img{
      height: 100%;
      display: block;
    }
  }
  &__bottom{
    padding-top: 3.56em;
    margin-top: auto;
  }
}

.withdraw{
  @include grid100gap(1.5em);
  &__grid{
    display: grid;
    grid-template-columns: calc(100% - 19.125em) 17.625em;
    gap: 1.5em;
    justify-content: space-between;
  }
}

.withdraw-form{
  @include grid100gap(.75em);
  max-width: 35.25em;
  &__nav{
    display: flex;
    margin-top: .75em;
  }
}

.withdraw-info{
  border-radius: 2em;
  padding: 1.5em;
  background-color: $bg;
  box-shadow: $shadow;
  @include grid100gap(.25em);
}

.wallets{
  margin-top: 1.5em;
  &__grid{
    margin-top: 1.25em;
  }
}

.wallet-card,
.add-wallet{
  padding: 1.5em;
  background-color: $bg;
  border-radius: 2em;
  box-shadow: $shadow;
}

.wallet-card{
  position: relative;
  &__remove{
    position: absolute;
    top: .5em;
    right: .5em;
    width: 3em;
    height: 3em;
    border-radius: 50%;
    border: .25em solid $sup;
    background-color: #fff;
    @include center;
    &:hover{
      path{
        stroke: $black;
      }
    }
    svg{
      width: 1.5em;
      height: auto;
    }
  }
  &__img{
    padding: 1em 1.5em;
    height: 6.3125em;
    border-radius: 1.5em;
    background-color: #fff;
    @include center;
    img{
      max-width: 100%;
      max-height: 100%;
    }
  }
  &__content{
    margin-top: 1.5em;
    @include grid100gap(.25em);
  }
}

.add-wallet{
  height: 14em;
  @include center;
  flex-direction: column;
  &:hover{
    box-shadow: $shadow2;
  }
  .advantage-card__icon{
    margin-bottom: 2.44em;
    background-color: #fff;
  }
}

.transaction{
  margin-top: 1.5em;
  padding: 1.5em;
  border-radius: 2em;
  box-shadow: $shadow;
  background-color: #fff;
}

.transaction-table{
  margin-top: 1.5em;
  table{
    width: 100%;
    border-spacing: 0 .75em;
  }
  th{
    text-align: left;
    font-size: 1em;
    line-height: 140%;
    color: $grayText;
    font-weight: 400;
  }
  tbody{
    tr{
      box-shadow: $shadow;
      border-radius: 1.5em;
    }
    td{
      padding-right: 1em;
      &:first-child{
        border-top-left-radius: 1.5em;
        border-bottom-left-radius: 1.5em;
        padding-left: 1em;
      }
      &:last-child{
        border-top-right-radius: 1.5em;
        border-bottom-right-radius: 1.5em;
        text-align: right;
      }
    }
  }
  td{
    padding: .94em 0;
    background-color: $bg;
  }
  &__title{
    display: none;
  }
  &__status span,
  &__title,
  &__desc{
    font-size: 1em;
    line-height: 140%;
  }
  &__status{
    color: #506D32;
    display: inline-flex;
    align-items: center;
    &._muted{
      color: $grayText;
    }
    img{
      width: 1em;
      flex-shrink: 0;
      margin-right: .5em;
    }
  }
}


@media screen and (max-width: 992px){
  .lk{
    padding-top: 5em;
    &__top{
      grid-template-columns: repeat(2,calc(50% - .75em));
    }
  }

  .stat{
    &__item{
      padding: .94em 1em;
      .text16{
        font-size: .875em;
      }
    }
  }

  .awards{
    &__steps{
      grid-template-columns: repeat(4,calc(25% - 1.5em / 4));
      gap: .5em;
    }
  }

  .wallet-card,
  .add-wallet{
    padding: 1em;
  }

  .add-wallet{
    height: 13em;
  }
}


@media screen and (max-width: 576px){
  .lk{
    padding-top: 3em;
    &__top{
      grid-template-columns: 100%;
      gap: 1em;
    }
    &__content{
      margin-top: 1em;
    }
  }

  .lk-greet{
    grid-column: 1/-1;
    padding: 1em;
    background-image: url(../img/greet-bg2.svg);
    background-size: auto 100%;
    background-position: right bottom;
    background-repeat: no-repeat;
    &__bottom{
      padding-top: 1.5em;
    }
  }

  .lk-card{
    padding: 1em;
    &__bottom{
      padding-top: 2em;
    }
    &._type2{
      flex-direction: row;
      justify-content: space-between;
      .lk-card__bottom{
        padding: 0 0 0 1em;
      }
    }
  }

  .lk-nav{
    display: block;
    padding: 0;
    border: none;
    &__tabs{
      padding: .75em;
      border-radius: 2em;
      border: 1px solid #D5D4CE;
    }
    &__ask{
      width: 100%;
      margin-top: 1em;
    }
  }

  .profile{
    grid-template-columns: 100%;
    gap: 1em;
  }

  .stat{
    padding: 1em;
  }

  .buy-stat{
    padding: 1em;
    margin-top: 1em;
    &__icon{
      margin-left: .5em;
    }
  }

  .awards{
    padding: 1em;
    &__steps{
      margin-top: 1em;
    }
  }

  .withdraw{
    padding: 1em;
    gap: 1em;
    &__grid{
      grid-template-columns: 100%;
    }
    &__sidebar{
      order: -1;
    }
  }

  .wallets{
    padding: 1em;
    &__grid{
      margin-top: 1em;
      display: flex;
      gap: unset;
      width: calc(100% + 2em);
      padding-left: 1em;
      padding-right: 1em;
      margin-left: -1em;
      @include fullScroll;
    }
  }

  .wallet-card{
    width: 13em;
    flex-shrink: 0;
    &:not(:first-child){
      margin-left: 1em;
    }
  }

  .add-wallet{
    margin-top: 1em;
    height: auto;
    padding: 1em;
    flex-direction: row;
    .advantage-card__icon{
      margin: 0 1.25em 0 0;
      flex-shrink: 0;
    }
  }

  .transaction{
    padding: 1em;
  }

  .transaction-table{
    margin-top: 1em;
    thead{
      display: none;
    }
    table{
      display: block;
    }
    tbody{
      @include grid100gap(.75em);
    }
    tr{
      background-color: $bg;
      padding: .94em 1em;
      @include grid100gap(.5em);
    }
    td{
      background-color: transparent;
      padding: 0 !important;
      text-align: left !important;
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
    &__status span,
    &__title,
    &__desc{
      font-size: .875em;
    }
    &__title{
      display: block;
      color: $grayText;
    }
  }
}
