.text60{
  font-size: 3.75em;
  line-height: 110%;
}

.text h2,
.text48{
  font-size: 3em;
  line-height: 110%;
}

.text h3,
.text40{
  font-size: 2.5em;
  line-height: 110%;
}

.text h4,
.text32{
  font-size: 2em;
  line-height: 110%;
}

.text h5,
.text24{
  font-size: 1.5em;
  line-height: 110%;
}

.text18{
  font-size: 1.125em;
  line-height: 110%;
}

.text li,
.text p,
.text16{
  font-size: 1em;
  line-height: 140%;
}

.text14{
  font-size: .875em;
  line-height: 140%;
}

.text12{
  font-size: .75em;
  line-height: 140%;
}

.lh140{
  line-height: 140%;
}

.bold-text{
  font-weight: 700;
}

.gray-text{
  color: $grayText;
}


@media screen and (max-width: 992px){
  .text24-tablet{
    font-size: 1.5em;
  }

  .text14-tablet{
    font-size: .875em;
  }
}


@media screen and (max-width: 576px){
  .text32{
    font-size: 1.5em;
  }

  .text18-mob{
    font-size: 1.125em;
  }

  .text60,
  .text32-mob{
    font-size: 2em;
  }

  .text48-mob{
    font-size: 3em;
  }

  .text24-mob{
    font-size: 1.5em;
  }

  .text14-mob{
    font-size: .875em;
  }
}
