.faq{
  padding: 7.5em 0 8.75em;
  position: relative;
  .container{
    position: relative;
    z-index: 2;
  }
  .text60{
    text-align: center;
  }
  &__inner{
    display: grid;
    grid-template-columns: 49.5em 17.625em;
    gap: 1.5em;
    justify-content: space-between;
    margin-top: 2.94em;
  }
  &__items{
    @include grid100gap(.75em);
  }
}

.qa-card{
  padding: 1.5em;
  border-radius: 1.5em;
  background-color: #fff;
  border: $border;
  &._active{
    .qa-card__btn{
      background-color: $bg;
    }
    ._hide{
      opacity: 0;
    }
  }
  &__show{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__btn{
    flex-shrink: 0;
    margin-left: 1.5em;
    width: 3em;
    height: 3em;
    border-radius: 50%;
    @include center;
    background-color: $primary;
    svg{
      width: 1.5em;
      height: auto;
    }
  }
  &__hidden{
    margin-top: .5em;
    display: none;
  }
}

.faq-news{
  padding: 1.5em;
  border-radius: 2em;
  border: 1px solid #D5D4CE;
  box-shadow: $shadow;
  &__nav{
    display: flex;
    margin-top: 1.5em;
  }
}


@media screen and (max-width: 992px){
  .faq{
    padding-top: 5em;
    &__inner{
      grid-template-columns: calc(100% - 15.9375em - 1.5em) 15.9375em;
    }
  }
}


@media screen and (max-width: 576px){
  .faq{
    padding: 3em 0 5em;
    &__inner{
      grid-template-columns: 100%;
    }
  }

  .qa-card{
    padding: 1em;
    .text24{
      font-size: 1.125em;
    }
  }

  .faq-news{
    padding: 1em;
  }
}
