.how-works{
  padding: 6.25em 0 8.75em;
  overflow: hidden;
  &__inner{
    display: grid;
    grid-template-columns: 30.375em calc(100% - 31.875em);
    gap: 3em 1.5em;
  }
  &__grid{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 3.935em));
    gap: 6.25em 7.87em;
  }
}


.how-buy-item,
.how-work-item{
  &__num{
    padding: 0 .75em;
    display: inline-flex;
    align-items: center;
    height: 2em;
    background-color: $primary;
    border-radius: 1em;
    box-shadow: $shadow;
    flex-shrink: 0;
  }
  &__text{
    @include grid100gap(.5em);
  }
}

.how-work-item{
  position: relative;
  &:nth-child(2){
    .how-work-item__bg{
      top: -6.25em;
      right: -4.06em;
      left: unset;
      width: 17.8125em;
    }
  }
  &:nth-child(3){
    .how-work-item__bg{
      top: -2.23em;
      left: -8.22em;
      width: 14.95669em;
    }
  }
  &:nth-child(4){
    .how-work-item__bg{
      top: -1.56em;
      right: -4.56em;
      left: unset;
      width: 14.5em;
    }
  }
  &__bg{
    position: absolute;
    top: -5em;
    left: -5.75em;
    width: 14.125em;
    z-index: -1;
  }
  &__text{
    margin-top: 1.5em;
  }
  &__nav{
    display: flex;
    margin-top: 1.5em;
  }
}

.info{
  &__inner{
    padding: 6.25em 5em 0;
    background-color: #fff;
    border-radius: 1.5em;
    box-shadow: $shadow;
  }
  .text60{
    text-align: center;
  }
  &__items{
    margin-top: 2em;
  }
}

.info-card{
  border-radius: 2em;
  padding: 1.5em;
  background-color: $bg;
  box-shadow: $shadow;
  &__img{
    height: 15.625em;
    display: flex;
    justify-content: center;
    img{
      height: 100%;
      width: auto;
      display: block;
    }
  }
  &__content{
    margin-top: 2em;
    @include grid100gap(.5em);
  }
}

.app{
  margin-top: 6.25em;
  display: grid;
  grid-template-columns: 19em calc(100% - 20.5em);
  gap: 1.5em;
  &:first-child{
    margin-top: 0;
  }
  &__img{
    img{
      width: 100%;
      display: block;
    }
  }
  &__content{
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    padding-top: 2.37em;
  }
  &__info{
    max-width: 24em;
  }
  &__stores{
    display: flex;
    margin-top: 2em;
  }
  &__item{
    height: 3em;
    flex-shrink: 0;
    @include lastM(0 .75em 0 0);
    img{
      height: 100%;
      width: auto;
      display: block;
    }
  }
}

.app-qr{
  padding: 1.38em 1em 1em;
  border-radius: 1.5em;
  border: $border;
  text-align: center;
  &__img{
    width: 6.75em;
    margin: 0 auto 1.37em;
    max-width: 100%;
    img{
      width: 100%;
      display: block;
    }
  }
}


@media screen and (max-width: 992px){
  .how-works{
    .text60{
      text-align: center;
    }
    &__inner{
      grid-template-columns: 100%;
    }
    &__grid{
      grid-template-columns: repeat(2,calc(50% - .75em));
      gap: 4.25em 1.5em;
    }
  }

  .how-work-item{
    &:nth-child(2){
      .how-work-item__bg{
        top: -5.44em;
        right: -.56em;
        width: 14.25em;
      }
    }
    &:nth-child(3){
      .how-work-item__bg{
        top: -4.46em;
        left: unset;
        right: -.69em;
        width: 14.66em;
        transform: rotate(15deg);
      }
    }
    &:nth-child(4){
      .how-work-item__bg{
        right: -.72em;
        width: 11.6em;
      }
    }
    &__bg{
      width: 11.3em;
      left: unset;
      right: 1.89em;
      top: -3.38em;
    }
  }

  .info{
    &__inner{
      padding: 3em 3em 0;
    }
  }

  .info-card{
    &__img{
      height: auto;
      img{
        width: 100%;
        height: auto;
      }
    }
    @media screen and (min-width: 577px){
      display: flex;
      align-items: flex-start;
      &__img{
        width: 15.625em;
        flex-shrink: 0;
      }
      &__content{
        margin: 0 0 0 1.5em;
      }
    }
  }

  .app{
    grid-template-columns: 18.625em calc(100% - 18.625em);
    gap: 0;
    &__content{
      padding: 0;
      display: block;
    }
    &__stores{
      margin-top: 1em;
    }
    &__item{
      height: 2.75em;
    }
  }

  .app-qr{
    margin-top: 1.94em;
    padding: 1em;
    display: flex;
    align-items: center;
    text-align: left;
    &__img{
      width: 4.75em;
      margin: 0 1em 0 0;
    }
  }
}


@media screen and (max-width: 576px){
  .how-works{
    padding: 3.75em 0 2.625em;
    &__inner{
      gap: 2em;
    }
    &__grid{
      grid-template-columns: 100%;
      gap: 3em;
    }
  }

  .how-work-item{
    &:nth-child(2){
      .how-work-item__bg{
        top: -2.12em;
        right: -.25em;
        width: 10.25em;
      }
    }
    &:nth-child(3){
      .how-work-item__bg{
        top: -2.06em;
        right: -1.36em;
        width: 11.11em;
      }
    }
    &:nth-child(4){
      .how-work-item__bg{
        top: -1.63em;
        right: 0;
        width: 9.6875em;
      }
    }
    &__bg{
      width: 8.4375em;
      right: 0;
      top: -1.63em;
    }
  }

  .info{
    &__inner{
      padding: 1em 1em 0;
    }
    &__items{
      margin-top: 1em;
    }
  }

  .info-card{
    padding: 1em;
    &__img{
      width: 100%;
    }
    &__content{
      margin-top: 1em;
    }
  }

  .app{
    margin-top: 4.25em;
    grid-template-columns: 100%;
    gap: 1.5em;
    &__img{
      order: 2;
    }
    &__item{
      height: 2.5em;
    }
  }

  .app-qr{
    margin-top: 1.5em;
    &__img{
      margin-right: 1em;
    }
  }
}
