.auth{
  height: $vh;
  display: grid;
  grid-template-columns: 54.4375em calc(100% - 54.4375em);
  &__img{
    background-size: cover;
    background-position: center;
    border-radius: 2em 0 0 2em;
  }
  &__content{
    @include center;
    position: relative;
    padding: 5em 10%;
  }
  &__logo{
    position: absolute;
    top: 2em;
    left: 50%;
    transform: translateX(-50%);
    width: 10.5625em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__inner{
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 31.25em;
    max-width: 100%;
  }
  &__title{
    text-align: center;
  }
}

.auth-nav{
  border-radius: 2em;
  border: 1px solid #D5D4CE;
  padding: .75em;
  display: flex;
  align-items: center;
  margin-bottom: 2em;
  &__link{
    display: flex;
    align-items: center;
    padding: 0 1.25em;
    height: 3.25em;
    border-radius: 1.5em;
    box-shadow: $shadow;
    font-weight: 700;
    transition: all .3s;
    cursor: pointer;
    @include lastM(0 .25em 0 0);
    span{
      font-size: 1em;
      line-height: 140%;
    }
    &:hover{
      color: $grayText;
    }
    &._active{
      pointer-events: none;
      background-color: $black;
      color: #fff;
    }
  }
}

.auth-form{
  margin-top: 2em;
  width: 100%;
  @include grid100gap(.75em);
  &._mt0{
    margin-top: 0;
  }
  &__nav{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: .75em;
    .text16{
      text-align: right;
      margin-left: 1em;
    }
  }
  &__resend{
    color: $grayText;
    display: inline-flex;
    margin-right: auto;
    &:hover{
      color: $black;
    }
  }
  .m-check{
    margin-top: .75em;
  }
}

.phone-field{
  position: relative;
  z-index: 2;
  &._active{
    .phone-field__dropdown{
      opacity: 1;
      pointer-events: unset;
    }
    .phone-field__arrow{
      transform: rotate(-180deg);
    }
  }
  .fg{
    input{
      padding-left: 9em;
    }
    label{
      left: 9em;
    }
  }
  &__toggle{
    cursor: pointer;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    left: .25em;
    height: 3.375em;
    width: 7.75em;
    border-radius: 1.25em;
    background-color: $sup;
    box-shadow: $shadow;
    padding: 0 .75em;
    display: flex;
    align-items: center;
  }
  &__flag{
    width: 1.5em;
    flex-shrink: 0;
    margin-right: .5em;
  }
  &__code{
    margin-right: .5em;
  }
  &__arrow{
    margin-left: auto;
    width: 1em;
    transition: all .3s;
  }
  &__dropdown{
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
    position: absolute;
    top: 100%;
    left: 0;
    margin-top: .44em;
    width: 100%;
    padding: 1.25em 0;
    background-color: #fff;
    border-radius: 1.5em;
    border: $border;
    box-shadow: $shadow;
    @include grid100gap(.75em)
  }
}

.phone-country,
.nice-select .option{
  &:before{
    content: '';
    position: absolute;
    top: 50%;
    left: 0;
    transform: translateY(-50%);
    pointer-events: none;
    width: 100%;
    height: calc(100% + 1em);
    background-color: $bg;
    opacity: 0;
    transition: all .3s;
  }
  &:after{
    content: '';
    flex-shrink: 0;
    margin-left: auto;
    width: 1em;
    height: 1em;
    background-image: url(../img/check.svg);
    @include bgImgSize(100%);
    transition: all .3s;
    opacity: 0;
  }
}

.phone-country{
  display: flex;
  align-items: center;
  padding: 0 1.25em;
  position: relative;
  &:hover{
    cursor: pointer;
    &:before{
      opacity: 1;
    }
  }
  &._active{
    &:after{
      opacity: 1;
    }
  }
  & * {
    position: relative;
    z-index: 2;
  }
}

.login-way{
  width: 100%;
  margin-top: 2.5em;
  display: flex;
  align-items: center;
  justify-content: space-between;
  .socials{
    margin-left: .5em;
    flex-shrink: 0;
  }
}

.socials{
  &._type2{
    .socials__item{
      background-color: #fff;
      &:hover{
        box-shadow: $shadow;
      }
    }
  }
}

.sms-hint{
  margin-bottom: .25em;
  @include grid100gap(.25em);
  .text16{
    margin-bottom: .25em;
  }
}



@media screen and (max-width: 1700px){
  .auth{
    grid-template-columns: 45% 55%;
  }
}


@media screen and (max-width: 992px){
  .auth{
    padding: 2em 7em;
    grid-template-columns: 100%;
    height: auto;
    &__img{
      display: none;
    }
    &__content{
      padding: 0;
      display: block;
    }
    &__logo{
      transform: unset;
      position: static;
      display: block;
      margin: 0 auto;
    }
    &__inner{
      width: 33.375em;
      margin: 13em auto 0;
    }
  }
}


@media screen and (max-width: 992px){
  .auth{
    padding: 1.5em;
    &__inner{
      margin-top: 3em;
    }
  }

  .auth-form{
    margin-top: 1.5em;
  }

  .login-way{
    margin-top: 2em;
  }
}
