.catalog-item{
  padding: 5em 0 8.75em;
  position: relative;
  .container{
    position: relative;
    z-index: 2;
  }
  &__top{
    text-align: center;
    display: flex;
    align-items: center;
    flex-direction: column;
  }
  &__back{
    display: flex;
    align-items: center;
    margin-bottom: 1em;
    color: $black;
    span{
      font-size: 1em;
      line-height: 140%;
    }
    svg{
      width: 1.5em;
      height: auto;
      flex-shrink: 0;
      margin-right: 1em;
    }
    &:hover{
      color: $grayText;
      path{
        stroke: $grayText;
      }
    }
  }
  .catalog__bg{
    width: 12.125em;
    top: 2.87em;
    left: 11.87em;
  }
  .catalog__bg2{
    width: 15em;
    top: 3em;
    right: 8.37em;
  }
  &__inner{
    margin-top: 4.25em;
    display: grid;
    grid-template-columns: 24em calc(100% - 31.87em);
    justify-content: space-between;
  }
}

.product-info{
  padding: 1.5em;
  border-radius: 2em;
  background: #FFF;
  box-shadow: $shadow;
  @include grid100gap(1em);
  &__top{
    padding: .75em;
    border-radius: 1.5em;
    border: $border;
  }
  &__img{
    height: 5.5em;
    @include center;
    img{
      max-width: 100%;
      max-height: 100%;
    }
  }
  .rating-block{
    margin-top: 1em;
  }
  &__desc{
    @include grid100gap(.5em);
    border-radius: 1.5em;
    padding: 1.5em;
    background-color: $bg;
    box-shadow: $shadow;
  }
  &__nums{
    display: flex;
    align-items: center;
    &-split{
      margin: 0 .5em;
      font-weight: 700;
    }
  }
  &__btn{
    width: 100%;
    justify-content: space-between;
  }
}

.share{
  margin-top: 1.5em;
  display: flex;
  align-items: center;
  justify-content: space-between;
  &__items{
    display: flex;
    align-items: center;
  }
  &__item{
    width: 1.5em;
    flex-shrink: 0;
    @include lastM(0 1em 0 0);
    svg{
      width: 100%;
      height: auto;
    }
    &:hover{
      path{
        fill: $black;
      }
      ._stroke-hover{
        fill: none;
        stroke: $black;
      }
    }
  }
}

.other-products{
  margin-top: 2em;
  &__items{
    margin-top: 1em;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .375em));
    gap: .75em;
  }
  &__item{
    display: block;
    text-align: center;
    padding: .5em 1em;
    border-radius: 2em;
    background: #FFF;
    box-shadow: $shadow;
    &:hover{
      box-shadow: $shadow2;
      color: $black;
    }
    &-img{
      height: 3em;
      margin-bottom: .25em;
      @include center;
      img{
        max-width: 100%;
        max-height: 100%;
      }
    }
  }
}

.m-check,
.advantage-card__desc,
.rate-item__text{
  button,
  a{
    color: inherit;
    border-bottom: 1px solid $primary;
    display: inline-block;
    padding-bottom: .175em;
    &:hover{
      color: inherit;
      border-color: transparent;
    }
  }
}

.advantage-card{
  padding: 1em;
  border-radius: 2em;
  background-color: #fff;
  box-shadow: $shadow;
  display: flex;
  flex-direction: column;
  &__content{
    @include grid100gap(.25em);
  }
  &__bottom{
    margin-top: auto;
    padding-top: 2em;
  }
  &__icon{
    width: 3.75em;
    height: 3.75em;
    border-radius: 50%;
    @include center;
    background-color: $bg;
    box-shadow: $shadow;
    img{
      width: 1.5em;
    }
  }
  &._dark{
    background-color: $black;
    color: #fff;
  }
}

.rate{
  margin-top: 4.25em;
  &__items{
    margin-top: 1.5em;
    padding: 1.5em;
    border-radius: 1.5em;
    border: 1px solid #D5D4CE
  }
}

.rate-item{
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1em 0;
  border-bottom: 1px solid #D5D4CE;
  &:first-child{
    padding-top: 0;
  }
  &:last-child{
    padding-bottom: 0;
    border-bottom: none;
  }
  &__num{
    margin-left: 2em;
    flex-shrink: 0;
    height: 3em;
    padding: 0 .75em;
    background-color: #fff;
    border-radius: 2em;
    box-shadow: $shadow;
    display: flex;
    align-items: center;
    &-inner{
      display: flex;
      align-items: flex-start;
    }
    .text24{
      margin: 0 .166em 0 .33em;
    }
    .text16{
      line-height: 110%;
    }
  }
}

.check-cashback{
  margin-top: 4.25em;
  padding: 1.5em;
  border-radius: 2em;
  background: #B5CC9E;
  box-shadow: $shadow;
  &__form{
    margin-top: 1.5em;
    display: grid;
    grid-template-columns: calc(100% - 12.5em) 11.75em;
    gap: .75em;
  }
  &__hint{
    margin-top: .75em;
    padding: 1.19em 1em;
    display: flex;
    align-items: center;
    border-radius: 2em;
    border: 1px solid rgba(12, 19, 30, 0.15);
    img{
      width: 1.5em;
      flex-shrink: 0;
      margin-right: .5em;
    }
  }
}

.term{
  margin-top: 4.25em;
  .text{
    margin-top: 1.5em;
  }
  &__nav{
    margin-top: 2em;
    display: flex;
  }
  ._hide{
    display: none;
  }
  &._show-all{
    ._hide{
      display: block;
    }
    .term__nav{
      display: none;
    }
  }
}

.term-group{
  @include lastM(0 0 2em);
}

.more-btn{
  display: flex;
  align-items: center;
  color: $grayText;
  &:hover{
    color: $black;
    path{
      stroke: $black;
    }
  }
  span{
    font-size: 1em;
    line-height: 140%;
  }
  svg{
    width: 1.5em;
    height: auto;
    margin-left: .5em;
  }
}

.how-buy{
  margin-top: 4.25em;
  &__items{
    margin-top: 1.5em;
    @include grid100gap(1.5em);
  }
}

.how-buy-item{
  display: flex;
  align-items: flex-start;
  padding: 1.5em;
  border-radius: 2em;
  background: #FFF;
  box-shadow: $shadow;
  position: relative;
  overflow: hidden;
  &__num{
    margin-right: 1.5em;
  }
  &__text{
    position: relative;
    z-index: 2;
  }
  &__img{
    position: absolute;
    top: 0;
    right: 0;
    width: 10.4375em;
    pointer-events: none;
  }
  &:nth-child(1){
    .how-buy-item__img{
      top: -1.9em;
      right: -1.28em;
      transform: rotate(15deg);
    }
  }
  &:nth-child(2){
    .how-buy-item__img{
      width: 8.625em;
    }
  }
  &:nth-child(3){
    .how-buy-item__img{
      right: .875em;
      width: 7.375em;
    }
  }
}


@media screen and (max-width: 1800px){
  .catalog-item{
    .catalog__bg{
      left: 1em;
    }
    .catalog__bg2{
      right: 1em;
    }
  }
}


@media screen and (max-width: 992px){
  .catalog-item{
    .catalog__bg{
      left: 2.06em;
      top: 7em;
      width: 11em;
    }
    .catalog__bg2{
      width: 12.375em;
      right: 1em;
      top: 3em;
    }
    &__inner{
      grid-template-columns: 15.9375em calc(100% - 17.4375em);
      gap: 3em 1.5em;
    }
  }

  .product-info{
    &__desc{
      padding: 1em;
    }
    &__btn{
      padding: 0;
      justify-content: center;
      img{
        display: none;
      }
    }
  }

  .advantages{
    grid-template-columns: 100%;
    gap: 1em;
  }

  .advantage-card{
    align-items: flex-start;
    flex-direction: row;
    justify-content: space-between;
    &__bottom{
      padding: 0;
      margin: 0 0 0 1em;
    }
  }

  .check-cashback{
    &__form{
      grid-template-columns: calc(100% - 5.25em) 4.5em;
    }
    &__btn{
      padding: 0;
      span{
        display: none;
      }
      img{
        margin: 0;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .catalog-item{
    padding: 3em 0 5em;
    &__inner{
      margin-top: 3em;
      grid-template-columns: 100%;
    }
  }

  .product-info{
    padding: 1em;
  }

  .other-products{
    margin-top: 3em;
    &__items{
      grid-template-columns: repeat(2,calc(50% - .5em));
      gap: 1em;
    }
  }

  .advantage-card{
    align-items: center;
  }

  .rate{
    margin-top: 3em;
    &__items{
      padding: 1em;
    }
  }

  .rate-item{
    align-items: flex-start;
    flex-direction: column;
    &__num{
      margin: 1em 0 0;
    }
  }

  .check-cashback{
    margin-top: 3em;
  }

  .term{
    margin-top: 3em;
  }

  .how-buy{
    margin-top: 3.625em;
    &__items{
      gap: 1em;
    }
  }
}
