.text{
  h2, h3, h4, h5, h6{
    font-weight: 700;
  }
  h2{
    margin-bottom: .5em;
  }
  h3{
    margin-bottom: 1.67em;
  }
  h4{
    margin-bottom: .5em;
  }
  h5{
    margin-bottom: .67em;
  }
  h6{
    font-size: 1.125em;
    line-height: 140%;
    margin-bottom: .89em;
  }
  p{
    @include lastM(0 0 2em);
  }
  ul{
    @include lastM(0 0 1.5em);
  }
  ul{
    @include grid100gap(1em);
    li{
      position: relative;
      padding-left: 1.75em;
      &:before{
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 1em;
        height: 1em;
        background-image: url(../img/li-marker.svg);
        @include bgImgSize(100%);
      }
    }
  }
  hr{
    display: block;
    opacity: 0;
    height: 2.25em;
  }
  img{
    max-width: 100%;
    display: block;
  }
}


@media screen and (max-width: 576px){
  .text{
    h2{
      font-size: 2em;
      margin-bottom: .75em;
    }
    h3{
      font-size: 1.5em;
      margin-bottom: 1em;
    }
    h4{
      font-size: 1.125em;
      margin-bottom: .67em;
    }
    h5{
      font-size: 1.125em;
      margin-bottom: .27em;
    }
  }
}
