.footer{
  padding: 8.75em 2em 2em;
  color: #ABAEB2;
  &__inner{
    border-radius: 1.5em;
    background-color: $black;
    padding: 6.25em 0;
    overflow: hidden;
    position: relative;
  }
  &__bg,
  &__bg2{
    @include centerAbsolute;
    width: 100%;
    pointer-events: none;
  }
  &__bg2{
    display: none;
  }
  .container{
    position: relative;
    z-index: 2;
  }
  &__top,
  &__bottom{
    display: grid;
    grid-template-columns: 38em calc(100% - 38em);
  }
  &__logo{
    display: block;
    width: 10.5625em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__bottom{
    margin-top: 6.69em;
    align-items: flex-end;
    .footer-nav{
      align-items: flex-end;
    }
  }
}

.footer-nav{
  display: grid;
  gap: 1.5em;
  grid-template-columns: repeat(2,calc(50% - .75em));
  &__title{
    color: #fff;
  }
  &__links{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-top: 1em;
  }
  &__link{
    display: flex;
    align-items: center;
    @include lastM(0 0 .5em);
    img{
      width: 1em;
      flex-shrink: 0;
      margin-right: .75em;
    }
  }
}

.socials{
  display: flex;
  align-items: center;
  &__item{
    width: 3em;
    height: 3em;
    border-radius: 50%;
    background: rgba(255, 255, 255, 0.15);
    @include center;
    @include lastM(0 .5em 0 0);
    &:hover{
      background: rgba(255, 255, 255, 0.5);
    }
    img{
      width: 1.5em;
      display: block;
    }
  }
}


@media screen and (max-width: 992px){
  .footer{
    padding: 8.75em 1.5em 1.5em;
    &__top,
    &__bottom{
      grid-template-columns: 14.4em calc(100% - 14.4em);
    }
    &__inner{
      padding: 3.75em 0 3em;
    }
    &__bottom{
      margin-top: 5em;
    }
  }
}


@media screen and (max-width: 576px){
  .footer{
    padding: 5em 1.25em 1.25em;
    &__top,
    &__bottom{
      grid-template-columns: 100%;
      gap: 1.5em;
    }
    &__inner{
      padding: 2em 1em;
    }
    .container{
      padding: 0;
    }
    &__logo{
      margin: 0 auto;
    }
    &__bottom{
      margin-top: 2em;
      gap: 2em;
      .footer-nav{
        gap: .5em;
      }
    }
    &__bg{
      display: none;
    }
    &__bg2{
      display: block;
    }
  }

  .footer-nav{
    grid-template-columns: 100%;
  }
}
