.blog-item,
.blog{
  position: relative;
  .container{
    position: relative;
    z-index: 2;
  }
  .text60{
    text-align: center;
  }
}

.blog{
  padding: 7.5em 0 8.75em;
  .catalog__bg{
    width: 12.25em;
  }
  .catalog__bg2{
    width: 14.1875em;
    top: 0;
  }
  &__items{
    margin-top: 1.5em;
  }
}

.blog-nav{
  margin-top: 4.25em;
  padding: .5em .75em .75em;
  border-radius: 2em;
  border: 1px solid #D5D4CE;
  background: #F3F1E9;
  display: flex;
  flex-wrap: wrap;
  .auth-nav__link{
    margin: .25em .25em 0 0;
  }
}

.blog-card{
  border-radius: 1.5em;
  background-color: #fff;
  box-shadow: $shadow;
  display: flex;
  flex-direction: column;
  &__top{
    position: relative;
  }
  .pagination__btn{
    position: absolute;
    width: 3.875em;
    height: 3.875em;
    right: 1.5em;
    bottom: 1.5em;
  }
  &__img{
    border-radius: 1.5em 1.5em 0 0;
    @include imgCover(57.3%);
  }
  &__content{
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    padding: 1.5em;
  }
  .text24{
    margin-bottom: 1em;
  }
  &__bottom{
    margin-top: auto;
    border-radius: 1.5em;
    padding: .75em;
    border: $border;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}

.blog-item{
  padding: 5em 0 4.25em;
  overflow: hidden;
  &__top{
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}

.article{
  margin-top: 4.25em;
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 1em 1.5em;
    border-radius: 1.5em;
    background-color: #fff;
    border: $border;
    .share{
      margin: 0;
      &__items{
        margin-left: 1.5em;
      }
    }
  }
  &__img{
    display: block;
    border-radius: 2em;
    margin-top: 1.5em;
    width: 100%;
  }
}

.article-content{
  margin: 4.25em auto 0;
  width: 49.5em;
  max-width: 100%;
}

.article-images,
.article-slider{
  img{
    object-fit: cover;
    width: 100%;
    min-height: 100%;
    display: block;
    border-radius: 1.5em;
  }
}

.article-images{
  margin: 3em 0;
  display: grid;
  grid-template-columns: repeat(2,calc(50% - .75em));
  gap: 1.5em;
  &:first-child{
    margin-top: 0;
  }
}

.article-slider{
  margin: 4.25em 0;
  overflow: hidden;
  position: relative;
  .swiper{
    &-container{
      width: calc(100% + 1em);
    }
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      padding-right: 1em;
      width: 100%;
      height: auto;
    }
    &-button{
      border-radius: 1.5em;
      img{
        width: 1.5em;
        min-height: auto;
      }
      &-next{
        right: 1.5em;
      }
      &-prev{
        left: 1.5em;
      }
    }
  }
}

.quote{
  border-radius: 1.5em;
  background: #FFF;
  box-shadow: $shadow;
  padding: 3em;
  display: flex;
  align-items: flex-start;
  margin: 3em 0;
  &:first-child{
    margin-top: 0;
  }
  &:before{
    content: '';
    width: 3em;
    height: 3em;
    flex-shrink: 0;
    background-image: url(../img/quote.svg);
    @include bgImgSize(100%);
    margin-right: 6.75em;
  }
  &__text{
    @include grid100gap(1.5em);
  }
}

.blog-collection{
  padding: 4.25em 0 8.75em;
  overflow: hidden;
}

.blog-slider{
  margin-top: 3em;
  .swiper{
    &-container{
      width: calc(100% + 1.5em);
    }
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      width: calc(100% / 3);
      padding-right: 1.5em;
      height: auto;
      opacity: 0;
      pointer-events: none;
      @media screen and (min-width: 993px){
        &-active,
        &-next,
        &-next + .swiper-slide{
          opacity: 1;
          pointer-events: unset;
        }
      }
    }
  }
  .blog-card{
    height: 100%;
  }
}


@media screen and (max-width: 1800px){
  .blog{
    .catalog__bg{
      left: 1em;
    }
    .catalog__bg2{
      right: 1em;
    }
  }
}


@media screen and (max-width: 992px){
  .blog{
    padding-top: 5em;
    .catalog__bg{
      width: 9.125em;
      top: 1.5em;
    }
    .catalog__bg2{
      width: 10.1875em;
      top: 1.5em;
    }
  }

  .blog-nav{
    justify-content: center;
  }

  .blog-item{
    padding-top: 5em;
  }

  .blog-slider{
    .swiper{
      &-slide{
        width: 50%;
        @media screen and (min-width: 677px){
          &-active,
          &-next{
            opacity: 1;
            pointer-events: unset;
          }
        }
      }
    }
  }
}


@media screen and (max-width: 576px){
  .blog{
    padding: 3em 0 5em;
    &__items{
      margin-top: 3em;
      grid-template-columns: 100%;
      gap: 1em;
    }
  }

  .blog-nav{
    position: relative;
    margin-top: 3em;
    padding: .75em;
    flex-wrap: nowrap;
    justify-content: unset;
    overflow-x: auto;
    &::-webkit-scrollbar {
      width: 0;
      height: 0;
    }
    .auth-nav__link{
      margin-top: 0;
      flex-shrink: 0;
    }
    &:after{
      content: '';
      position: absolute;
      width: 2.0625em;
      height: 100%;
      right: 0;
      top: 0;
      background: linear-gradient(90deg, rgba(243, 241, 233, 0.00) 0%, #F3F1E9 66.67%);
      pointer-events: none;
    }
  }

  .blog-card{
    &__img{
      padding-bottom: 68.75%;
    }
    &__content{
      padding: 1em;
    }
  }

  .blog-item{
    padding: 3em 0 2.5em;
  }

  .article{
    margin-top: 3em;
    &__top{
      flex-wrap: wrap;
      .share{
        width: 100%;
        order: 2;
        margin-top: 1em;
      }
    }
  }

  .article-images{
    display: flex;
    gap: unset;
    @include fullScroll;
    img{
      width: 16.5em;
      flex-shrink: 0;
      &:not(:last-child){
        margin-right: 1em;
      }
    }
  }

  .article-slider{
    overflow: unset;
    .swiper{
      &-container{
        width: 100%;
      }
      &-button{
        display: none;
      }
      &-slide{
        width: 17.5em;
        height: 18.75em;
      }
    }
  }

  .quote{
    padding: 1em;
    &:before{
      margin-right: 0;
    }
  }

  .blog-collection{
    padding: 2.5em 0 5em;
  }

  .blog-slider{
    margin-top: 1.5em;
    .swiper{
      &-container{
        width: 100%;
      }
      &-slide{
        pointer-events: unset;
        opacity: 1;
        width: 17.5em;
        padding-right: 1em;
      }
    }
    .blog-card__img{
      padding-bottom: 83%;
    }
  }
}
