.modal{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $vh;
    overflow-y: auto;
    background-color: $bg;
    transition: all .3s;
    opacity: 0;
    pointer-events: none;
    padding: 8.75em 1.5em;
    @media screen and (min-width: 993px){
        text-align: center;
        &:before {
            content: '';
            display: inline-block;
            height: 100%;
            vertical-align: middle;
        }
        &__dialog{
            display: inline-block;
            text-align: left;
            vertical-align: middle;
        }
    }

    &._active{
        opacity: 1;
        pointer-events: unset;
    }
    &__close{
        z-index: 3;
        position: fixed;
        width: 5em;
        height: 5em;
        top: 2em;
        right: 2em;
        @include center;
        background: #FFF;
        border-radius: 1.5em;
        box-shadow: $shadow;
        &:hover{
            background-color: $primary;
        }
        svg{
            width: 1.5em;
            height: auto;
        }
    }
    &__top{
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    &__title{
        text-align: center;
    }
    &__dialog{
        width: 36.75em;
        max-width: 100%;
        position: relative;
        z-index: 2;
    }
    &__bg,
    &__bg2{
        position: absolute;
        pointer-events: none;
    }
    &__bg{
        top: 0;
        left: 0;
        width: 23.6%;
    }
    &__bg2{
        position: absolute;
        right: 0;
        bottom: 0;
        width: 25.1%;
    }
}

.faq-form{
    margin-top: 2em;
    @include grid100gap(1.5em);
    &__nav{
        display: flex;
    }
}

.thank-faq{
    &__img{
        display: block;
        width: 15.625em;
        margin: 0 auto;
    }
    &__text{
        @include grid100gap(1em);
        text-align: center;
        margin-top: 1.5em;
    }
}

.wallet-modal{
    .modal__dialog{
        width: 62.25em;
    }
    &__items{
        margin-top: 3em;
    }
}

.pay-system{
    border-radius: 1.5em;
    background: #FFF;
    box-shadow: $shadow;
    padding: 1.5em;
    &__img{
        padding: 1em;
        border-radius: 1.5em;
        border: $border;
        height: 6.3125em;
        @include center;
        img{
            max-width: 100%;
            max-height: 100%;
        }
    }
    &__bottom{
        margin-top: 1.5em;
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
    &__text{
        @include grid100gap(.25em);
    }
    &__btn{
        flex-shrink: 0;
        margin-left: 1em;
        width: 3.875em;
        height: 3.875em;
        border-radius: 1.5em;
        @include center;
        background-color: $bg;
        box-shadow: $shadow;
        img{
            width: 1.5em;
        }
        &:hover{
            background-color: $primary;
        }
    }
}

.wallet-form{
    margin-top: 3em;
    padding: 1.5em;
    border-radius: 2em;
    background: #FFF;
    box-shadow: $shadow;
    display: grid;
    grid-template-columns: 16.6875em calc(100% - 19.6875em);
    gap: 3em;
    &__img{
        padding: 3.2em;
        border-radius: 1.5em;
        border: $border;
        @include center;
        img{
            max-width: 100%;
            max-height: 100%;
        }
    }
    &__top{
        @include grid100gap(.25em);
    }
    &__items{
        margin-top: 1.5em;
        @include grid100gap(.75em)
    }
    &__nav{
        display: flex;
        margin-top: 1.5em;
    }
}

.wallet-code{
    display: grid;
    grid-template-columns: calc(100% - 13.8125em - .75em) 13.8125em;
    gap: .5em;
    .text16{
        margin: .25em 0 .5em;
    }
}


@media screen and (max-width: 992px){
    .modal{
        &__bg,
        &__bg2{
            display: none;
        }
    }

    .wallet-modal{
        &__items{
            grid-template-columns: 100%;
        }
    }

    .pay-system{
        @media screen and (min-width: 577px){
            display: flex;
            &__img{
                width: 16.75em;
                flex-shrink: 0;
                margin-right: 1.5em;
            }
            &__bottom{
                display: block;
                flex-grow: 1;
                margin: 0;
            }
            &__btn{
                margin: -.75em 0 0 auto;
            }
        }
    }

    .wallet-code{
        margin-top: .75em;
        grid-template-columns: 100%;
    }
}


@media screen and (max-width: 576px){
    .modal{
        padding: 4.25em 1.25em;
        &__close{
            top: .5em;
            right: .5em;
            width: 3em;
            height: 3em;
            svg{
                width: 1em;
            }
        }
    }

    .thank-faq{
        &__img{
            width: 10em;
        }
        .text18{
            font-size: 1em;
        }
    }

    .wallet-modal{
        &__items{
            gap: 1em;
            margin-top: 1.5em;
        }
    }

    .pay-system{
        padding: 1em;
        &__bottom{
            margin-top: 1em;
        }
    }

    .wallet-form{
        padding: 1em;
        margin-top: 1.5em;
        grid-template-columns: 100%;
        gap: 1em;
        &__img{
            height: 10em;
        }
    }
}
