@import 'vars';
@import 'components/_text.scss';
@import 'components/_buttons.scss';
@import "components/_inputs.scss";
.awards__steps,
.profile,
.grid-column2,
.grid-column3,
.grid-column4{
  display: grid;
  gap: 1.5em;
}

.profile,
.grid-column2{
  grid-template-columns: repeat(2,calc(50% - .75em));
}

.grid-column3{
  grid-template-columns: repeat(3,calc(100% / 3 - 1em));
}

.awards__steps,
.grid-column4{
  grid-template-columns: repeat(4,calc(25% - 4.5em / 4));
}


@media screen and (max-width: 992px){
  .grid-column2{
    grid-template-columns: 100%;
  }

  .grid-column4{
    grid-template-columns: repeat(3,calc(100% / 3 - 1em));
  }

  .grid-column2-tablet{
    grid-template-columns: repeat(2,calc(50% - .75em));
  }

  .grid-column3-tablet{
    grid-template-columns: repeat(3,calc(100% / 3 - 1em));
  }
}


@media screen and (max-width: 576px){
  .grid-column2,
  .grid-column3{
    gap: 1em;
  }

  .grid-column3{
    grid-template-columns: repeat(2,calc(50% - .5em));
  }
}
