@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0 1.5em;
    height: 3.875em;
    border-radius: 1.5em;
    box-shadow: $shadow;
    font-weight: 700;
    &:disabled{
        opacity: .4;
        pointer-events: none;
    }
    &:focus {
        box-shadow: none;
    }
    span{
        font-size: 1.125em;
        line-height: 140%;
    }
    img,
    svg{
        width: 1.5em;
        height: auto;
        flex-shrink: 0;
    }
    &._small{
        height: 3.25em;
        padding: 0 1.25em;
        span{
            font-size: 1em;
        }
    }
}

.m-btn-white,
.m-btn-black,
.m-btn-primary{
    position: relative;
    overflow: hidden;
    &:before{
        content: '';
        position: absolute;
        right: .81em;
        width: 80%;
        height: 100%;
        top: 0;
        background-color: rgba(0, 0, 0, 0.15);
        filter: blur(25px);
        transition: inherit;
        opacity: 0;
    }
    &:hover{
        &:before{
            opacity: 1;
        }
    }
}


.m-btn-black {
    background-color: $black;
    color: #fff;
    &:hover {
        color: #fff;
        background-color: rgba($black,.8);
    }
}

.m-btn-primary{
    background-color: $primary;
    color: $black;
    &:hover{
        color: $black;
    }
}

.m-btn-white {
    background-color: #fff;
    color: $black;
    &:hover {
        color: $black;
        background-color: $primary;
    }
}

.swiper-button{
    position: absolute;
    width: 3.25em;
    height: 3.25em;
    border-radius: 50%;
    img{
        width: 1.5em;
    }
}

.arrow-link{
    display: flex;
    align-items: center;
    font-weight: 700;
    &:hover{
        color: $primary;
        path{
            stroke: $primary;
        }
    }
    svg{
        width: 1.5em;
        height: auto;
        margin-left: 1em;
    }
    span{
        font-size: 1.125em;
        line-height: 140%;
    }
}

.m-btn-arrow{
    img{
        width: 1.5em;
        flex-shrink: 0;
        margin-left: .5em;
    }
}
