.fg {
  position: relative;
  margin: 0;
  textarea,
  .nice-select,
  input {
    border: 2px solid #fff;
    border-radius: 1.5rem;
    box-shadow: $shadow;
    padding: 0 1.25em;
    height: 3.25em;
    width: 100%;
    font-size: 1em;
    line-height: 140%;
    background-color: #fff;
    color: #000;
    &::placeholder {
      color: #828B9A;
    }
    &:focus {
      border-color: $primary;
    }
  }
  label{
    color: $grayText;
    span{
      font-size: 1em;
      line-height: 130%;
    }
  }
  .nice-select{

    display: flex;
    align-items: flex-end;
    padding-bottom: .67em;
  }
  textarea{
    padding-top: 1.25em;
    padding-bottom: 1.25em;
    height: 16.25em;
    resize: none;
  }
  &._type2{
    .nice-select{
      height: 3.875em;
      &:before{
        content: '';
        position: absolute;
        width: 1em;
        height: 1em;
        top: 50%;
        transform: translateY(-50%);
        right: 1.25em;
        background-image: url(../img/arrow-bottom-black.svg);
        @include bgImgSize(100%);
      }
    }
    textarea,
    select,
    input{
      height: 3.875em;
      padding-top: 1.5em;
      &._filled,
      &:focus{
        &~label{
          top: .59em;
          span{
            font-size: .875em;
          }
        }
      }
    }
    textarea{
      height: 16.25em;
    }
    label{
      transition: all .2s;
      position: absolute;
      top: 1.25em;
      left: 1.25em;
    }
  }
  .list{
    margin-top: .5em;
    padding: 1.25em 0;
    border-radius: 1.5em;
    border: $border;
    box-shadow: $shadow;
    @include grid100gap(.75em);
  }
  .option{
    padding: 0 1.25em;
    font-size: 1em;
    line-height: 140%;
    border: none !important;
    background-color: transparent !important;
    position: relative;
    font-weight: 400 !important;
    &:before{
      content: '';
      position: absolute;
      top: 50%;
      left: 0;
      transform: translateY(-50%);
      pointer-events: none;
      width: 100%;
      height: calc(100% + 1em);
      background-color: $bg;
      opacity: 0;
      transition: all .3s;
      z-index: -1;
    }
    &:after{
      position: absolute;
      right: 1.25em;
      top: 50%;
      transform: translateY(-50%);
    }
    &.selected{
      &:after{
        opacity: 1;
      }
    }
    &:hover{
      &:before{
        opacity: 1;
      }
    }
  }
}

.fg-gray{
  textarea,
  input,
  .nice-select{
    border: 1px solid $sup;
  }
}

.rating-block{
  @include center;
  .rating{
    margin-left: .5em;
  }
}

.rating-block{
  &._disabled{
    pointer-events: none;
  }
}

.rating{
  cursor: pointer;
  &__stars{
    width: 7.5em;
    max-width: 100%;
    position: relative;
    img{
      width: 100%;
      display: block;
    }
  }
  &__fill{
    width: 0;
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    overflow: hidden;
    img{
      width: 7.5em;
      display: block;
    }
  }
}

.m-check{
  position: relative;
  input{
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
  }
}

.m-check{
  input {
    &:checked+label {
      &::before {
        background-color: $primary;
        border-color: $primary;
        background-size: 1em auto;
      }
    }
  }
  label{
    position: relative;
    cursor: pointer;
    display: block;
    padding-left: 2.0625em;
    span{
      font-size: 1em;
      line-height: 130%;
    }
    &:before,
    &:after{
      pointer-events: none;
    }
    &:before{
      content: '';
      position: absolute;
      z-index: 1;
      left: 0;
      top: 0;
      transition: all .100s;
      background-color: #fff;
      background-image: url(../img/check-black.svg);
      @include bgImgSize(0);
      width: 1.25em;
      height: 1.25em;
      box-shadow: $shadow;
      border-radius: .5em;
      border: 2px solid #fff;
    }
    a{
      position: relative;
      z-index: 2;
    }
  }
  &._error{
    input:not(:checked){
      &~label:before{
        border-color: $red;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .m-check{
    label{
      span{
        font-size: .875em;
      }
    }
  }
}
