.catalog{
  padding: 8.43em 0 8.75em;
  position: relative;
  &__bg,
  &__bg2{
    position: absolute;
  }
  &__bg{
    top: 2.62em;
    left: 8.27em;
    width: 15.3125em;
  }
  &__bg2{
    top: 1em;
    right: 7.85em;
    width: 15.1875em;
  }
  &__top{
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__count{
    padding: 0 1.1875em;
    height: 3.375em;
    display: flex;
    align-items: center;
    position: relative;
    margin-bottom: 1.5em;
    img{
      width: 1.5em;
      flex-shrink: 0;
      margin-right: .76em;
    }
    &:before,
    &:after{
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border-radius: 1.875em;
      z-index: -1;
    }
    &:before{
      transform: rotate(-8deg);
      background-color: #fff;
    }
    &:after{
      transform: rotate(-2deg);
      background-color: #B5CC9E;
    }
  }
  &__inner{
    margin-top: 5.55em;
    display: grid;
    grid-template-columns: 17.625em calc(100% - 19.125em);
    gap: 1.5em;
  }
}

.choose-us{
  display: grid;
  grid-template-columns: 25.375em calc(100% - 26.875em);
  gap: 1em 1.5em;
}

.catalog-nav{
  @include grid100gap(.5em);
  &__item{
    &._open{
      .catalog-nav__arrow{
        transform: rotate(180deg);
      }
    }
  }
  &__show,
  &__link{
    display: flex;
    align-items: center;
    min-height: 2.5em;
    span{
      font-size: 1em;
      line-height: 140%;
    }
    &:hover{
      color: $grayText;
      rect,
      path{
        fill: $grayText;
      }
      ._stroke-hover{
        stroke: $grayText;
        fill: none;
      }
      ._stroke{
        rect,
        path{
          stroke: $grayText;
          fill: none;
        }
      }
    }
  }
  &__show{
    transition: all .3s;
    cursor: pointer;
  }
  &__icon{
    width: 1.5em;
    height: auto;
    margin-right: .5em;
  }
  &__arrow{
    width: 1em;
    flex-shrink: 0;
    margin-left: .5em;
  }
  &__hidden{
    padding-top: .25em;
    display: none;
  }
  &__sublinks{
    display: flex;
    flex-wrap: wrap;
    width: calc(100% + .5em);
    margin-top: -.5em;
  }
  &__sublink{
    display: flex;
    align-items: center;
    padding: 0 .75em;
    height: 2em;
    border-radius: 2em;
    background-color: #fff;
    box-shadow: $shadow;
    margin: .5em .5em 0 0;
    span{
      font-size: .875em;
      line-height: 140%;
    }
    &:hover{
      color: $grayText;
    }
    &._active{
      background-color: $primary;
      pointer-events: none;
    }
  }
}

.pagination{
  margin-top: 3em;
  display: flex;
  justify-content: space-between;
  &__buttons,
  &__items{
    display: flex;
  }
  &__item,
  &__btn{
    width: 3.75em;
    height: 3.75em;
    border-radius: 1.5em;
    @include center;
  }
  &__item{
    border: 1px solid transparent;
    @include lastM(0 .25em 0 0);
    span{
      font-size: 1em;
      line-height: 140%;
    }
    &:hover{
      border-color: #D9D9D9;
      color: $black;
    }
    &._active{
      background-color: $primary;
      pointer-events: none;
      font-weight: 700;
      box-shadow: $shadow;
    }
  }
  &__btn{
    background-color: #fff;
    box-shadow: $shadow;
    @include lastM(0 .75em 0 0);
    &:hover{
      background-color: $primary;
    }
    img{
      width: 1.5em;
    }
  }
}

.filter{
  &__toggle{
    display: none;
  }
}


@media screen and (max-width: 1800px){
  .catalog{
    &__bg{
      left: 1em;
    }
    &__bg2{
      right: 1em;
    }
  }
}


@media screen and (max-width: 992px){
  .choose-us{
    grid-template-columns: 17.25em calc(100% - 18.75em);
  }

  .catalog{
    padding-top: 5.93em;
    &__bg{
      left: 2em;
      top: 10.2em;
      width: 11.5625em;
    }
    &__bg2{
      right: 1.45em;
      top: 4em;
      width: 11em;
    }
    &__inner{
      margin-top: 4.3em;
      grid-template-columns: 100%;
    }
  }

  .filter{
    position: relative;
    &._open{
      .filter__toggle{
        box-shadow: none;
      }
      .filter__dropdown{
        opacity: 1;
        pointer-events: unset;
      }
      .filter__arrow{
        transform: rotate(-180deg);
      }
    }
    &__toggle{
      display: flex;
      align-items: center;
      min-height: 3.25em;
      border-radius: 2em;
      background: #FFF;
      padding: .5em 1.5em;
      box-shadow: $shadow;
      position: relative;
      z-index: 3;
    }
    &__icon{
      width: 1.5em;
      flex-shrink: 0;
      margin-right: .5em;
    }
    &__arrow{
      width: 1em;
      margin-left: auto;
      flex-shrink: 0;
      transition: all .3s;
    }
    &__dropdown{
      transition: all .3s;
      position: absolute;
      top: 0;
      border-radius: 2em;
      background-color: #fff;
      z-index: 2;
      padding: 3.38em 1.5em 1.5em;
      opacity: 0;
      pointer-events: none;
      width: 100%;
      box-shadow: $shadow;
    }
  }

  .catalog-nav{
    border-top: $border;
    gap: .375em;
    &__arrow{
      margin-left: auto;
    }
  }
}


@media screen and (max-width: 576px){
  .choose-us{
    grid-template-columns: 100%;
    .text40{
      text-align: center;
    }
  }

  .catalog{
    padding: 3em 0 5em;
    &__bg,
    &__bg2{
      display: none;
    }
    &__count{
      height: 3em;
      padding: 0 .75em;
      margin-bottom: 1em;
      &:before{
        transform: rotate(6.531deg);
      }
    }
    &__inner{
      margin-top: 3em;
    }
    .cashback-card2{
      &__top{
        min-height: 7.5em;
        .text16{
          font-size: .875em;
        }
      }
      &__img{
        height: 4.75em;
        display: flex;
        align-items: center;
        margin: 0;
      }
      &__bottom{
        padding: 0;
        background-color: transparent;
        display: block;
        text-align: center;
      }
      .cashback-size{
        margin: .5em 0 0;
        background-color: $bg;
      }
    }
  }

  .pagination{
    margin-top: 2em;
    ._hide-mob{
      display: none;
    }
  }
}
