.header{
  padding-top: 1.5em;
  &__inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__logo{
    width: 15.875em;
    display: block;
    img{
      max-width: 10.5625em;
      width: 100%;
      display: block;
    }
  }
  &__link{
    margin: 0 auto 0 .5em;
    height: 3.25em;
    white-space: nowrap;
    svg{
      margin-right: .75em;
    }
    ._view2{
      display: none;
    }
  }
}

.search-field{
  &__btn{
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 1.25em;
    width: 1.5em;
    svg{
      width: 100%;
      height: auto;
    }
    &:hover{
      path{
        stroke: $primary;
      }
    }
  }
}

.header-search{
  width: 37.5em;
  margin: 0 .5em 0 auto;
  position: relative;
  &._active{
    .header-search__dropdown{
      opacity: 1;
      pointer-events: unset;
    }
  }
  &__dropdown{
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
    position: absolute;
    top: 100%;
    left: 0;
    width: 100%;
    margin-top: .25em;
    padding: 1em;
    background-color: #fff;
    border-radius: 1.5em;
    box-shadow: $shadow;
  }
  &__links{
    display: flex;
    flex-wrap: wrap;
    margin-top: -.75em;
    width: calc(100% + .75em);
  }
  &__link{
    margin: .75em .75em 0 0;
    display: flex;
    align-items: center;
    padding: 0 .75em;
    height: 2.5em;
    border-radius: 2em;
    background-color: $bg;
    box-shadow: $shadow;
    span{
      font-size: 1em;
      line-height: 140%;
    }
    &:hover{
      color: $grayText;
    }
  }
}

.header-action{
  margin-left: 1em;
  display: flex;
  align-items: center;
  &__btn{
    display: flex;
    align-items: center;
    svg{
      width: 1.5em;
      height: auto;
      flex-shrink: 0;
      margin-right: .5em;
    }
    span{
      font-size: 1em;
      line-height: 140%;
    }
    &:hover{
      color: $primary;
      path{
        stroke: $primary;
      }
    }
    &:not(:last-child){
      position: relative;
      margin-right: 3em;
      &:after{
        content: '';
        position: absolute;
        height: 2.5em;
        width: 1px;
        background: rgba(12, 19, 30, 0.15);
        top: 50%;
        left: 100%;
        margin-left: 1.5em;
        transform: translateY(-50%);
        pointer-events: none;
      }
    }
  }
}


@media screen and (max-width: 1500px){
  .header-search{
    width: 28em;
  }
}


@media screen and (max-width: 992px){
  .header{
    &__inner{
      flex-wrap: wrap;
    }
    &__link{
      margin-right: 0;
      margin-top: 1.25em;
      order: 2;
      width: 11.75em;
      padding: 0;
      flex-shrink: 0;
    }
  }

  .header-search{
    order: 2;
    margin-left: 0;
    margin-top: 1.25em;
    width: calc(100% - 12.75em);
  }
}


@media screen and (max-width: 576px){
  .header{
    padding-top: 1em;
    &__logo{
      width: 9.125em;
    }
    &__link{
      order: unset;
      margin: 0 .75em;
      width: 5.9375em;
      svg{
        display: none;
      }
      span{
        font-size: .875em;
      }
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
  }

  .header-search{
    width: 100%;
    margin: .75em 0 0;
  }

  .header-action{
    margin-left: 0;
    &__login{
      display: none;
    }
    &__reg{
      width: 3.25em;
      height: 3.25em;
      border-radius: 50%;
      background-color: #fff;
      box-shadow: $shadow;
      justify-content: center;
      span{
        display: none;
      }
      svg{
        margin: 0;
      }
    }
  }
}
